@import '../../assets/css/variable.less';
.card-box{
  position: relative;
}
.new-symbol {
  width: 362px;
  height: 31px;
  position: absolute;
  top: 90px;
  left: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  background: #222021;
  padding: 0 14px;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  color: #FFB110;
  a{
    color: #FFB110;
    cursor: pointer;
  }
  .close-symbol{
    cursor: pointer;
  }
}


.card-list {
  cursor: pointer;

  a {
    text-decoration: none;
    background: url('../../assets/img/pool-card-background.svg');
  }

  .card-header.v3 {
    background-image: url(../../assets/img/new-label.svg);
    background-repeat: no-repeat;
    background-position: right top;
  }

  .card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    border-bottom: 1px solid @divider_color;
    padding: 0 24px;


    .pool-network-type {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .pool-type-network-text {
        margin-left: 12px;
        font-weight: 600;

        .pool-type-text {
          color: #FFFFFF;
        }

        .pool-network-text {
          font-size: 14px;
        }
      }

      .network-logo {
        img {
          width: 28px;
          height: 28px;
        }
      }
    }

    .base-token-logo {
      height: 48px;

      .v3-tokens {
        display: flex;

        .tokens {
          overflow: hidden;
          width: 120px;
          white-space: nowrap;
        }
      }

      .base-token-box-bg {
        width: 48px;
        height: 48px;
        display: inline-block;
        border-radius: 50%;
        background-color: #0b182d;
        position: relative;

        .base-token-bg {
          width: 40px;
          height: 40px;
          background-color: rgba(255, 255, 255, 0.1);
          border-radius: 50%;
          display: flex;
          color: @whiteFontColor;
          align-items: center;
          position: absolute;
          justify-content: center;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);

          img {
            width: 20px;
            height: 20px;
          }
        }

      }

      .base-token-box-bg:nth-child(n) {
        margin-left: -12px;
      }

      .base-token-box-bg:first-child {
        margin-left: 0;
      }

      .lp-base-token-bg {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 24px;

        .lp-con {
          margin: 0 5px;
        }

        .base-token-bg {
          width: 40px;
          height: 40px;
          background-color: rgba(255, 255, 255, 0.1);
          border-radius: 50%;
          display: flex;
          align-items: center;


        }
      }

      .WBNB,
      .BNB,
      .WMATIC,
      .amUSDC,
      .BUSD,
      .USDT,
      .CAKE,
      .DERI,
      .TBUSD,
      .SXP,
      .WBNB,
      .ETH,
      .LTC,
      .XRP,
      .BTCB,
      .ADA,
      .CAKE,
      .MATIC,
      .AAVE,
      .TUSD,
      .DAI,
      .DOT,
      .LINK,
      .XVS,
      .USDC,
      .TRX,
      .TDERI,
      .UST,
      .DOGE,
      .LUNA {
        background-size: 20px 20px;
        background-repeat: no-repeat;
        background-position: center;
      }

      .LTC {
        background-image: url('../../assets/img/LTC.svg');
      }
      .DOGE {
        background-image: url('../../assets/img/DOGE.svg');
      }
      .UST {
        background-image: url('../../assets/img/UST.svg');
      }
      .LUNA {
        background-image: url('../../assets/img/LUNA.svg');
      }

      .XRP {
        background-image: url('../../assets/img/XRP.svg');
      }

      .XVS {
        background-image: url('../../assets/img/XRP.svg');
      }

      .DAI {
        background-image: url('../../assets/img/DAI.svg');
      }

      .DOT {
        background-image: url('../../assets/img/DOT.svg');
      }

      .LINK {
        background-image: url('../../assets/img/LINK.svg');
      }


      .ADA {
        background-image: url('../../assets/img/ADA.svg');
      }

      .AAVE {
        background-image: url('../../assets/img/AAVE.svg');
      }

      .TUSD {
        background-image: url('../../assets/img/TUSD.svg');
      }

      .TRX {
        background-image: url('../../assets/img/TRX.svg');
      }

      .SXP {
        background-image: url('../../assets/img/SXP.svg');
      }

      .USDC {
        background-image: url('../../assets/img/USDC.svg');
      }

      .DERI,
      .TDERI {
        background-image: url('../../assets/img/logo.svg');
      }

      .ETH {
        background-image: url('../../assets/img/ETHUSD.svg');
      }

      .BTCB {
        background-image: url('../../assets/img/BTCUSD.svg');
      }

      .WBNB,
      .BNB {
        background-image: url('../../assets/img/BNB.svg');
      }

      .WMATIC,
      .MATIC {
        background-image: url('../../assets/img/MATIC.svg');
      }

      .amUSDC {
        background-image: url('../../assets/img/amusdc.svg');
      }

      .BUSD,
      .TBUSD {
        background-image: url('../../assets/img/BUSD.svg');
      }

      .USDT {
        background-image: url('../../assets/img/USDT.svg');
      }

      .CAKE {
        background-image: url('./img/Pancake.svg');
      }
    }


  }

  .card-content {
    height: 404px;
    padding: 48px 24px 40px 24px;


    .max-apy {
      height: 67px;
      margin-top: 8px;
    }

    .max-apy-title,
    .total-liquidity-title {
      font-size: 14px;
    }

    .max-apy-num,
    .total-liquidity-num {
      font-size: 36px;
      color: @whiteFontColor;
      margin-top: 5px;
      height: 45px;
    }

    .total-pool-liquidity {
      margin-top: 24px;
    }

    .your-deri-epoch {
      font-size: 12px;
      height: 20px;
      margin-top: 48px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      span {
        margin-left: 8px;
      }

      .staked-box {
        width: 80px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px;
        border: 1px solid rgba(224, 236, 255, 0.5);
        border-radius: 100px;
      }

      .xvs-collectable {
        display: flex;
        align-items: center;
        margin-left: 0;

        .staked-box {
          width: 100%;
        }

      }
    }

    .stake-button {
      margin-top: 24px;
      border: 1px solid transparent;

      .button {
        width: 332px;
        height: 56px;
        border: 0;
        background: rgba(255, 255, 255, 0.1);
        color: #FFFFFF;
        border-radius: 4px;
        font-size: 18px;
        font-weight: 600;
      }

    }
  }
}

.open-list {
  cursor: not-allowed;
}

.card-box:hover {
  background: linear-gradient(to right bottom, rgba(33, 49, 86, 1), rgba(21, 37, 67, 0.5));
  background-repeat: no-repeat;
  background-size: 102%;
}

.card-box:hover .stake-button {
  width: 332px;
  background-color: rgba(85, 119, 253, 0.1);
  border: 1px solid @button_outline_color;
  border-radius: 4px;
}

.card-box:first-child {
  .card-header {
    .base-token-box-bg {
      background-color: #111e34;
    }
  }
}

html[lang=ko] {
  .card-list .card-content .your-deri-epoch .staked-box {
    width: 100px;
  }

  .card-list .card-content .your-deri-epoch .xvs-collectable .staked-box {
    width: 100%;
  }
}

html[lang=ja] {
  .card-list .card-content .your-deri-epoch .staked-box {
    width: 120px;
  }

  .card-list .card-content .your-deri-epoch .xvs-collectable .staked-box {
    width: 100%;
  }
}
html[lang=tr] {
  .card-list .card-content .your-deri-epoch .staked-box {
    width: 120px;
  }

  .card-list .card-content .your-deri-epoch .xvs-collectable .staked-box {
    width: 100%;
  }
}