@import '../../assets/css/variable.less';
@border: 1px solid @divider_color;

.pool-box {
  width: 100%;
  display: flex;
  justify-content: center;

  .pool-info {
    display: flex;
    margin: 0 auto;
  }

  .left {
    border: @border;
    // border-right: 0;
  }

  // .left-header-info,
  // .mining-claim,
  // .pool-tab {
  //   border-right: @border;
  // }

  .pool-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 96px;
    font-weight: normal;

    .select-pool-ves {
      display: none;
    }

    .pool-title-text {
      font-size: @32FontSize;
      line-height: 38px;
      color: #FFFFFF;
    }

    .pool-title-select-text {
      margin-left: 100px;

      .pool-test-des {
        font-size: 14px;
        line-height: 18px;
        text-align: right;
        width: 240px;
      }
    }

    .simple-select-ves {
      display: none;
    }
  }

  .pool-card-claim-bg {
    background: linear-gradient(135deg, rgba(97, 122, 255, 0.1) 0%, rgba(223, 229, 255, 0) 50.52%, rgba(85, 91, 237, 0.05) 100%);

    .pool-tab {
      height: 108px;
      border-bottom: 1px solid @divider_color ;
      // line-height: 108px;
      position: relative;

      .pool-tab-info {
        display: flex;
        height: 100%;

        .simple-tab-mining {
          display: none;


        }
      }

      .filter {
        display: flex;
        align-items: center;

        .filter-img {
          cursor: pointer;
        }
      }

      .tab-mining {
        display: flex;
        align-items: center;
        font-weight: bold;

        .pool-active,
        .pool-unactive {
          box-sizing: content-box;
          padding: 24px 24px;
          line-height: 0;
          font-size: 14px;
          cursor: pointer;
        }

        .pool-active {
          color: #FFFFFF;
          background: #203B60;
          border-radius: 4px;
        }
      }
    }

    .pool-card {
      display: flex;
      flex-flow: wrap;
      flex-grow: 1;
      padding-left: 24px;

      // border-right: 1px solid @divider_color;
      .card-box {
        width: 380px;
        height: 484px;
        border-bottom: 1px solid @divider_color;
        border-right: 1px solid @divider_color;
      }
    }

  }

  .right {
    border: 1px solid @divider_color;
    border-left: 0;
    display: none;
  }
}

* {
  // box-sizing: -box;
  box-sizing: border-box;
}

//小于375px
@media only screen and (max-width: 374px) {
  .pool-box {
    .pool-info {
      width: 375px;
    }

    .left-header-info {
      padding: 0 24px;

      .pool-title-select-text {
        display: none;
      }

      .simple-select-ves {
        display: block;
      }
    }

    .pool-card-claim-bg {
      .pool-tab {
        .pool-tab-info {
          display: flex;
          justify-content: space-between;
          padding: 0 24px;
          margin: 0 auto;

          .simple-tab-mining {
            display: flex;
          }

          .tab-mining {
            display: none;
          }
        }
      }

      .pool-card {
        padding: 0;

        .card-box:nth-child(3n+1) {
          width: 380px;
        }

      }
    }
  }
}

//小于760 大于375px
@media only screen and (min-width : 375px) and (max-width:759px) {
  .pool-box {
    .left,.right{
      border: 0;
    }
    .pool-info {
      width: 375px;
    }

    .left-header-info {
      padding: 0 24px;

      .pool-title-select-text {
        display: none;
      }

      .simple-select-ves {
        display: block;
      }
    }

    .pool-card-claim-bg {
      .pool-tab {
        .pool-tab-info {
          display: flex;
          justify-content: space-between;
          padding: 0 24px;
          margin: 0 auto;

          .simple-tab-mining {
            display: flex;
            align-items: center;
            .pool-active,
            .pool-unactive {
              padding: 24px 24px;
              line-height: 0;
              font-size: 14px;
              height: 48px;
              cursor: pointer;
            }

            .pool-active {
              color: #FFFFFF;
              background: #203B60;
              border-radius: 4px;
            }
          }

          .tab-mining {
            display: none;
          }
        }
      }

      .pool-card {
        padding: 0;
        margin-bottom: 8px;
        .card-box:nth-child(3n+1) {
          width: 375px;
        }

        .card-box {
          width: 375px;
          border-right: 0;
        }

      }
    }
  }
}

//小于1140 大于760
@media only screen and (min-width : 760px) and (max-width:1139px) {
  .pool-box {
    .pool-info {
      width: 760px;
    }

    .left-header-info {
      padding: 0 24px;

      .pool-title-select-text {
        display: none;
      }

      .select-pool-ves {
        display: block;
        width: 330px;
        height: 56px;
        background: @divider_color;
        border-radius: 8px;
        line-height: 56px;
      }
    }

    .pool-card-claim-bg {
      min-height: 1216px;

      .pool-tab {
        .pool-tab-info {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 24px;
          margin: 0 auto;
        }
      }

      .pool-card {
        padding: 0;
        width: 760px;

        .card-box:nth-child(3n+1) {
          width: 380px;
        }

        .card-box:nth-child(3n) {
          border-right: 1px solid @divider_color;
        }

        .card-box:nth-child(2n) {
          border-right: 0;
        }

      }
    }
  }
}

//小于1568 大于1140
@media only screen and (min-width : 1140px) and (max-width: 1567px) {
  .pool-box {
    display: flex;
    flex-direction: column;

    .left {

      border-top: 1px solid @divider_color;
      border-bottom: 1px solid @divider_color;

      .left-header-info {
        width: 1140px;
        margin: 0 auto;
        padding: 0 24px;

        .select-pool-ves {
          display: block;
          width: 332px;
          height: 56px;
          background: @divider_color;
          border-radius: 8px;
          line-height: 56px;
        }
      }

      .pool-card-claim-bg {
        .pool-tab {
          .pool-tab-info {
            display: flex;
            width: 1140px;
            justify-content: space-between;
            margin: 0 auto;
            padding: 0 24px;
          }
        }

        .pool-card {
          padding-left: 0;
          width: 1140px;

          .card-box:nth-child(3n+1) {
            width: 380px;
            margin-left: 0px;
          }

          .card-box:first-child {
            border-right: 1px solid @divider_color;
          }

          .card-box:nth-child(3n) {
            border-right: 0;
          }
        }
      }
    }


  }

}

//小于2056 大于1568
@media only screen and (min-width : 1568px) {
  .pool-box {
    flex-direction: initial;

    .left {
      margin: 0;
      border-top: 1px solid @divider_color;

      .left-header-info {
        width: 1164px;
        padding-left: 48px;

        .pool-title {
          padding-right: 40px;
        }
      }

      .pool-card-claim-bg {
        width: 1164px;

        // height: 1216px;
        .pool-tab {
          padding-left: 48px;

          .filter {
            display: none;
          }
        }
      }

      .pool-card {
        .card-box {
          border-right: 1px solid @divider_color;
        }

        .card-box:nth-child(3n+1) {
          width: 403px;
          padding-left: 24px;
          margin-left: -24px;

          .card-header {
            width: 403px;
            margin-left: -24px;
            padding-left: 48px;
          }
        }

        .card-box:nth-child(3n) {
          border-right: 0px;
        }
      }
    }

    .right {
      display: block;

      .right-info {
        width: 100%;
        height: 1312px;

        .select-pool-ves-staked {
          border-bottom: 1px solid @divider_color;
          height: 176px;
          width: 100%;
          padding-right: 24px;
          padding-left: 24px;
          padding-top: 37px;

          .l-toggle .react-toggle-thumb {
            background: #203B60;
          }

          .l-toggle .react-toggle--checked .react-toggle-thumb {
            background: #5577FD;
          }

          .select-pool-ves {
            width: 332px;
            height: 56px;
            background: @divider_color;
            border-radius: 8px;
            line-height: 56px;
          }

          .staked-only {
            width: 332px;
            display: flex;
            justify-content: space-between;
            margin-top: 39px;

            .staked-only-text {
              color: #FFFFFF;
            }
          }

        }

        .select-pool-type-network {
          padding-left: 24px;
          border-bottom: 1px solid @divider_color;

          .pool-type {
            width: 380px;
            height: 184px;
          }

          .type-cols {
            width: 380px;
            height: 45px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-right: 48px;

            .type-title {
              font-size: 14px;
              color: #FFFFFF;
              display: flex;
              justify-content: space-between;
              align-items: center;

              img {
                margin-right: 10px;
                width: 22px;
                height: 22px;
              }
            }
          }

          .network {
            margin-top: 8px;
            margin-bottom: 16px;
          }
        }

        .select-footer {
          color: @second_color;
          font-size: 18px;

          .clear-all-filter {
            text-align: center;
            cursor: pointer;
            padding-top: 21px;
            height: 309px;
          }

          .deri-logo {
            height: 484px;
            position: relative;
            left: -1px;
            img{
              margin: 0 auto;
            }
          }
        }
      }
    }
  }
}

